<template>
  <div>
    <div class="bg-red-100" v-for="(item, name) in items" :key="item.name">{{ name }}</div>
    <div v-for="tag in tags" :key="tag.name">{{ tag.iconClass }}</div>
  </div>
</template>

<script>
const firebase = require("firebase/app");
require("firebase/firestore");
//const _ = require("lodash");

const firebaseConfig = {
  apiKey: "AIzaSyAnUJiHtfTdpVXSIYFmJBXYcpcEbjXiIIg",
  authDomain: "bin-listing.firebaseapp.com",
  databaseURL: "https://bin-listing.firebaseio.com",
  projectId: "bin-listing",
  storageBucket: "bin-listing.appspot.com",
  messagingSenderId: "565166920201",
  appId: "1:565166920201:web:736c913d96a436f6"
};

// Initialize Cloud Firestore through Firebase
firebase.initializeApp(firebaseConfig);
var firebasedb = firebase.firestore();

export default {
  data: function() {
    return {
      db: firebasedb,
      items: {},
      tags: {}
    };
  },
  mounted: function() {
    // this.items
    this.db
      .collection("items")
      .get()
      .then(snapshot => {
        snapshot.forEach(item => {
            this.items[item.id] = item.data()
        });
      });

    // this.tags
    this.db
      .collection("tags")
      .get()
      .then(snapshot => {
        snapshot.forEach(tag => {
          this.tags[tag.id] = tag.data()
        });
      });
  }
};
</script>

